header {
  background-image: url("https://bing.ioliu.cn/v1");
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 300px;
}
header .header-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 16px;
  color: white;
}
header .header-wrapper nav {
  float: left;
  line-height: 19px;
  padding: 8px 0;
}
header .header-wrapper nav li {
  list-style: none;
  float: left;
  margin-right: 16px;
}
header .header-wrapper nav li a {
  color: white;
  text-decoration: none;
}
header .header-wrapper nav li a.router-link-active {
  text-shadow: 1px 1px #efd7ef;
  font-size: 18px;
}
header .header-wrapper .header {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
header .header-wrapper .header .name {
  color: #fff;
  margin-top: 10px;
  font-size: 24px;
}
header .header-wrapper .signin-button,
header .header-wrapper .hello-tip {
  float: right;
  color: #fff;
}
header .header-wrapper .signin-button {
  background-color: transparent;
  border-radius: 5px;
  min-width: 80px;
}
header .header-wrapper .hello-tip .admin-entry {
  color: white;
  text-decoration: underline;
}
header .avatar {
  overflow: hidden;
  display: inline-block;
  width: 100px;
  height: 100px;
  border-radius: 200px;
}
header .avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}



