footer {
  width: 100%;
  padding: 16px 0;
  color: #999;
  text-align: center;
  font-size: 12px;
}
footer p {
  height: 16px;
  line-height: 16px;
}
footer p + p {
  margin-top: 5px;
}
footer p .icon-heart {
  display: inline-block;
  vertical-align: middle;
  color: red;
  animation: heart 0.83333333s ease-in-out infinite alternate;
}
footer p a {
  text-decoration: none;
  color: #999;
  border-left: 1px solid #999;
  margin-left: 5px;
  padding-left: 5px;
}
@keyframes heart {
  0% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}



